* {box-sizing: border-box;}

.header h1 a{
    font-size : 30px ;
    font-weight: bold;
    color: black; 
    text-transform: uppercase;
}

.header {
  overflow: hidden;
  font-size: 16px;
  text-transform: uppercase;
  background: white;
  font-weight: 600;
  letter-spacing: 1px;
  outline: none;
  background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
  padding: 20px 10px;
}

.header a {
  float: left;
  color: black;
  text-align: center;
  padding: 12px;
  text-decoration: none;
  font-size: 18px; 
  line-height: 25px;
  border-radius: 4px;
}
.header table{
    width : 100% ;
}
 .header img{
    height : 50px;
}

.header a:hover {
  background-color: #ddd;
  color: black;
}

.header a.active {
    background: linear-gradient(147deg, #fe8a39  0%, #fd3838 74%);
  color: white;
}

.header-right {
  float: right;
}

@media screen and (max-width: 500px) {
  .header a {
    float: none;
    display: block;
    text-align: left;
  }
  
  .header-right {
    float: none;
  }
}